import React, { Children, useMemo } from 'react';
import styled from 'styled-components';
import ConvertForm from '../components/ConvertForm';
import { Row, Col } from 'antd';
import { DEFAULT_MARKET, MarketProvider } from '../utils/markets';
import { useLocalStorageState } from '../utils/utils';
import { ConnectionProvider, useConnection } from 'utils/connection';
import { Connection } from '@solana/web3.js';
import { useWallet, WalletProvider } from 'utils/wallet';
import { LOGO_WHITE } from 'assets';
import { PRIMARY_PINK } from 'consts/colors.consts';

const Wrapper = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 16px 16px;
  .borderNone .ant-select-selector {
    border: none !important;
  }
`;

const DashboardWrapper = styled.div({
  marginTop: '40px',
  '.dash-title': {
    textAlign: 'center',
    h1: {
      fontWeight: 700,
      fontSize: '60px',

      img: {
        width: '300px',
      },
    },
  },

  '.dash-body': {
    gap: '20px',
    display: 'flex',
    justifyContent: 'center',
    button: {
      color: 'white',
      fontSize: '24px',
      fontWeight: 700,
      padding: '20px 48px',
      lineHeight: '32px',
      borderRadius: '12px',
      background: PRIMARY_PINK,
    },

    '.btn-sec': {
      border: `3px solid ${PRIMARY_PINK}`,
      background: 'none',
    },
  },
});

export default function DashboardPage() {
  return (
    <DashboardWrapper>
      <div className="dash-title">
        <h1>
          <div>Introducing</div> <img src={LOGO_WHITE} />
        </h1>
      </div>
      <div className="dash-body">
        <button>DEX Trading</button>
        <button className="btn-sec">Nora Swap</button>
      </div>
    </DashboardWrapper>
  );
}
